.header__row {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.header__logo {
    padding-left: 10px;
    display: flex;
    align-items: center;
}

.header__logoImg {
    padding: 5px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 50px;
    font-weight: 700;
    border: 1px solid black;
    background-color: white;
    border-radius: 15px;
    color: black;
}
.header__logoText {
    padding-left: 5px;
    font-size: 35px;
    font-weight: 700;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    -webkit-text-stroke: 2px #000;
    color: white;
}
.header__menu {
    display: flex;
}
.header__menu li {
    text-align: center;
    font-size: 20px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin: 10px;
    border-radius: 15px;
    padding: 10px;
    text-transform: uppercase;
}
.header__menu li a {
    color: black;
}