.portfolio__title {
    font-size: 35px;
    font-weight: 700;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    -webkit-text-stroke: 2px #000;
    color: white;
    text-align: center;
}

.portfolio__row {
    margin-top: 50px;
    display: flex;
    justify-content: space-around;
}

.portfolio__subTitle {
    font-size: 35px;
    text-align: center;
}

.portfolio__imgBox {
background-color: 1px solid black;
padding: 20px;
background-color: rgb(236, 165, 250);
border-radius: 20px;
border: 1px solid black;
}

.portfolio__img {
    height: 350px;
    width: 350px;
    border: 1px solid black;
    border-radius: 20px;
}
