.container {
    width: 1800px;
    margin: 0 auto;
    border: 1px solid black;
}

.header {
    height: 10%;
    background-color: rgb(236, 165, 250);
    display: flex;
    align-items: center;
}
.body {
    background-color: rgb(255, 255, 255);
    height: 85%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    box-shadow: inset 0px 0px 10px black;
}
.footer {
    height: 5%;
    background-color: rgb(236, 165, 250);
}

.wrapper {
    width: 100%;
    height: 100%;
}
