.contacts__title {
    font-size: 35px;
    font-weight: 700;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    -webkit-text-stroke: 2px #000;
    color: white;
    text-align: center;
}

.contacts__row {
    margin-top: 50px;
    display: flex;
    justify-content: space-around;
}
.contacts__col {
    border: 1px solid black;
    display: flex;
}
